<template>
  <div  v-if="serialNumber === ''"  >
    <p>Before doing backup operations a robot must be selected.</p>
  </div>
  <div  v-else>
    <BackupAgents v-on:clicked="showSnack" v-bind:serialNumber="serialNumber"></BackupAgents>
    <NewAgent v-on:clicked="showSnack" v-bind:serialNumber="serialNumber"></NewAgent>
    <BackupArchive v-on:clicked="showSnack" v-bind:serialNumber="serialNumber"></BackupArchive>
    <md-snackbar ref="snackbar" >
      <span> {{ errorMessage }} </span>
    </md-snackbar>

  </div>
</template>

<script>

  export default {

    props: ['serialNumber'],

    name: 'backup',
    data() {
      return {
        errorMessage: '',
      };
    },

    methods: {

      showSnack(message) {
        this.$refs.snackbar.open();
        this.errorMessage = message;
      },
    }
  };

</script>

<style scoped>

</style>



