import Vue from 'vue'
import App from './App'
import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.css'
import About from './components/About'
import BackupAgents from './components/BackupAgents'
import BackupArchive from './components/BackupArchive'
import Backups from './components/Backups'
import NewAgent from './components/NewAgent'
import Robots from './components/Robots'

Vue.use(VueMaterial);

Vue.component("About", About);
Vue.component("BackupAgents", BackupAgents);
Vue.component("BackupArchive", BackupArchive);
Vue.component("Backups", Backups);
Vue.component("NewAgent", NewAgent);
Vue.component("Robots", Robots);

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App }
})



