<template>
  <md-card md-with-hover>
    <md-card-header>
      <div class="md-title">Archive</div>
      <div class="md-subhead">There are {{ fileCount }} files in the archive</div>      
    </md-card-header>
    <md-card-expand>
      <md-card-content>
        <md-table>
          <md-table-header>
            <md-table-row >
              <md-table-head>Agent name</md-table-head >
              <md-table-head>Date</md-table-head >
              <md-table-head>Size (bytes)</md-table-head>
              <md-table-head class="iconCell">Download</md-table-head >
              <md-table-head class="iconCell">Delete</md-table-head >
            </md-table-row>
          </md-table-header>
          <md-table-body>
            <md-table-row v-for="backup in backups" :key="backup._id">
              <md-table-cell >
                {{backup.agentName}}
              </md-table-cell>

              <md-table-cell >
                {{backup.backupDate}}
              </md-table-cell>

              <md-table-cell  >
                {{backup.fileSize}}
              </md-table-cell>

              <md-table-cell class="iconCell">
                <md-button class="md-icon-button md-dense iconButton" v-on:click="downloadBackup(backup.fullFilePath)">
                  <md-icon >file_download</md-icon>
                </md-button>
              </md-table-cell>

              <md-table-cell class="iconCell">
                <md-button class="md-icon-button md-dense iconButton" v-on:click="deleteBackup(backup.fullFilePath)">
                  <md-icon >delete</md-icon>
                </md-button>
              </md-table-cell>

            </md-table-row>
          </md-table-body>

        </md-table>

      </md-card-content>

      <md-card-actions md-alignment="left" class="urButtons">
        <md-button class="md-icon-button" md-expand-trigger>
          <md-icon>keyboard_arrow_down</md-icon>
        </md-button>

      </md-card-actions>
    </md-card-expand>

  </md-card>
</template>

<script>

  import { stateService } from '../services/stateService';
  var config = require('../../../config/configuration');
  import { DashboardServiceClass } from '../services/dashboardService';
  const dashboardService = new DashboardServiceClass();

  export default {

    props: ['serialNumber'],

    watch: {
      serialNumber: function () {
        this.updateBackups();
      }
    },

    name: 'backup',
    data() {
      return {
        agents: [],
        allBackups: [],
        backups: [],
        fileCount: 0,
        errorMessage: '',
        name: '',
        file: '',
        frequency: 'Daily',
        keepForever: true,
        keepNumber: 20,
      };
    },

    created: function () {
      stateService.addUpdateListener(this.updateState.bind(this));
      stateService.requestUpdate(config.stateArchive);
    },

    methods: {

      apiResponse(result) {
        this.errorMessage = '';
        if (result.success) {
          // nothing
        } else {
          this.showSnack(result.error);
        }
      },

      deleteBackup(name) {
        dashboardService.deleteBackup(this.serialNumber, name, this.apiResponse.bind(this));
      },

      downloadBackup(name) {
        dashboardService.downloadBackup(this.serialNumber, name, this.apiResponse.bind(this));
      },

      isDownloadValid() {
        return false;
      },

      showSnack(message) {
        this.$emit('clicked', message);
      },
 
      updateBackups() {
        this.backups = [];
        for (let backup of this.allBackups) {
          if (backup.serialNumber === this.serialNumber) {
            this.backups.push(backup);
          }
        }
        this.fileCount = this.backups.length;
      },

      updateState(data) {
        const backups = stateService.getValue(data, config.stateArchive);
        if (backups) {
          this.allBackups = backups;
          this.updateBackups();
        }
      },

    }

  };
</script>

<style scoped>
  .urButtons { display: block;}

  .iconCell { 
    text-align: center;
  }

  .iconButton{
    position: static;
  }
  td {
    padding: 0.5em;
  }
</style>



