<template>
    <md-card md-with-hover>
      <md-card-header>
        <div class="md-title">Backup agents for robot {{ serialNumber }}</div>
      </md-card-header>
      <md-card-content>
        <md-table>
          <md-table-header>
            <md-table-row >
              <md-table-head>Agent name</md-table-head >
              <md-table-head>Path</md-table-head >
              <md-table-head>Frequence</md-table-head>
              <md-table-head>Keep backups</md-table-head>
              <md-table-head>Last backup</md-table-head>
              <md-table-head class="iconCell">Status</md-table-head >
              <md-table-head class="iconCell">Backup now</md-table-head >
              <md-table-head class="iconCell">Remove</md-table-head >
            </md-table-row>
          </md-table-header>
          <md-table-body>
            <md-table-row v-for="agent in agents" :key="agent._id">
              <md-table-cell >
                {{agent.name}}
              </md-table-cell>

              <md-table-cell >
                {{agent.file}}
              </md-table-cell>

              <md-table-cell  >
                {{agent.frequency}}
              </md-table-cell>

              <md-table-cell  v-if="agent.keepForever">
                forever
              </md-table-cell>
              <md-table-cell  v-else>
                {{agent.keepNumber}}
              </md-table-cell>

              <md-table-cell  >
                {{agent.lastBackup}}
              </md-table-cell>

              <md-table-cell  class="iconCell">
                {{agent.status}}
              </md-table-cell>

              <md-table-cell class="iconCell">
                <md-button class="md-icon-button md-dense iconButton" v-on:click="runBackupAgent(agent._id)">
                  <md-icon >replay</md-icon>
                </md-button>
              </md-table-cell>

              <md-table-cell class="iconCell">
                <md-button class="md-icon-button md-dense iconButton" v-on:click="removeBackupAgent(agent._id)">
                  <md-icon >close_circle</md-icon>
                </md-button>
              </md-table-cell>

            </md-table-row>
          </md-table-body>
        </md-table>
        <md-progress v-if="uploadRunning" md-indeterminate></md-progress>
      </md-card-content>


    </md-card>
</template>

<script>

  import { stateService } from '../services/stateService';
  const config = require('../../../config/configuration');
  import { DashboardServiceClass } from '../services/dashboardService';
  const dashboardService = new DashboardServiceClass();
  
  export default {

    props: ['serialNumber'],

    watch: {
      serialNumber: function () {
        this.updateAgents();
      }
    },

    name: 'backupAgents',
    data() {
      return {
        agents: [],
        allAgents: [],
        errorMessage: '',
        uploadRunning: false
      };
    },

    created: function () {
      stateService.addUpdateListener(this.updateBackupsScreen.bind(this));
      stateService.requestUpdate(config.stateBackupAgents);
    },

    methods: {

      apiResponse(result) {
        this.errorMessage = '';
        this.uploadRunning = false;
        if (result.success) {
          this.showSnack(result.success);
        } else {
          this.showSnack(result.error);
        }
      },

      removeBackupAgent(_id) {
        dashboardService.removeBackupAgent(_id, this.apiResponse.bind(this));
      },

      runBackupAgent(_id) {
        this.uploadRunning = true;
        dashboardService.runBackupAgent(_id, this.apiResponse.bind(this));
      },

      showSnack(message) {
        this.$emit('clicked', message);
      },
      
      updateAgents() {
        this.agents = [];
        for (let agent of this.allAgents) {
          if (agent.serialNumber === this.serialNumber) {
            console.log(agent)
            
            this.agents.push(agent);
          }
        }
      },

      updateBackupsScreen(data) {
        const allAgents = stateService.getValue(data, config.stateBackupAgents);
        if (allAgents) {
          this.allAgents = allAgents;
          this.updateAgents();
        }
      },

    }

  };
</script>

<style scoped>
  .urButtons { display: block;}

  .iconCell { 
    text-align: center;
  }

  .iconButton{
    position: static;
  }
  td {
    padding: 0.5em;
  }
</style>



