import Vue from 'vue';
import VueResource from 'vue-resource';

const config = require('../../../config/configuration');

Vue.use(VueResource);

export class webServiceClass {

  constructor() {
    this.connectionHandler = null;
    this.connected = false;
    this.trying = false;
    this.connectionTimer = this.tryConnectionTimer();
  }

  tryResult(result) {
    this.trying = false;
    if (!result.hasOwnProperty('error')) {
      clearInterval(this.connectionTimer);
      this.connectionTimer = null;
      this.setConnectionState(true);
    }
  }

  tryConnection() {
    if (!this.trying) {
      this.trying = true;
      this.API('GET', 'api-version', this.tryResult.bind(this));
    }
  }

  tryConnectionTimer() {
    return setInterval(() => {
      this.tryConnection()
    }, 1000);
  }

  setConnectionState(state) {
    if (!state && (this.connectionTimer) === null) {
      this.connectionTimer = this.tryConnectionTimer();
    }
    if (this.connectionHandler && this.connected !== state) {
      this.connectionHandler(state);
    }
    this.connected = state;
  }

  API(action, command, done) {
    var result = '';
    var apiHost = this.getAPIHost();
    var url = '//' + apiHost + '/' + command;
    Vue.http({url: url, method: action, })
            .then(
                    (response) => {
              this.setConnectionState(true);
              if (done) {
                const jsonText = response.bodyText;
                var json = null;
                try {
                  json = JSON.parse(jsonText);
                } catch (e) {
                  json = {error: 'Invalid server response'};
                }
                done(json);
              }
            },
                    (response) => {
              this.setConnectionState(false);
              if (done) {
                done({error: response.statusText});
              }
            });
    return result;
  }

  BlobRequestAPI(action, command, done) {
    var result = '';
    var apiHost = this.getAPIHost();
    var url = '//' + apiHost + '/' + command;
    Vue.http({url: url, method: action, responseType: 'blob'})
            .then(
                    (response) => {
              result = response.data;
              this.setConnectionState(true);
              if (done) {
                done({success: response});
              }
            },
                    (response) => {
              this.setConnectionState(false);
              if (done) {
                done({error: response.statusText});
              }
            });
    return result;
  }

  getAPIHost() {
    var host = window.location.host;
    var ix = host.indexOf(":");
    if (ix >= 0) {
      host = host.substring(0, ix);
    }
    var port = config.webAPIPort;
    host += ":" + port;
    return host;
  }

  setConnectionHandler(handler) {
    this.connectionHandler = handler;
  }

}



