<template>
  <div  >
    <div style="float:left" >
      <h3>About Robot Control Center</h3>
      <p >Copyright (C) 2018, Kim Nyholm SL.</p>
      <p >Version {{ version }}.</p>
      <p >For feature requests, updates and support go to <a target="_blank" href="http://kimnyholm.com/universal-robots">http://KimNyholm.com</a>.</p>
      <md-card>
        <md-card-content>
          <p>{{license.type}} license</p>
          <ul>
            <li v-if="serialNumber !== ''"> Robot serial number: {{ serialNumber }}</li>
            <li v-if="license.licenseKey !== ''">Key: {{license.licenseKey}}</li>
            <li v-if="license.maintenanceValidTo !== ''">Maintenance and support expires {{ license.maintenanceValidTo }}</li>
          </ul>
        </md-card-content>
      </md-card>
    </div>
    <div>
      <a target="_blank" href="http://kimnyholm.com">
        <img style="width:150px; height:150px" src="../assets/KN-logo-icon-blue2000x2000-transperent-150x150.png">
      </a>
    </div>
  </div>
</template>

<script>

  import { stateService } from '../services/stateService';
  var config = require('../../../config/configuration');


  export default {
    name: 'About',
    data() {
      return {
        version: "'Waiting for server reponse'",
        serialNumber: '',
        license: {
          "maintenanceValidTo": "",
          "type": "Evaluation",
          "licenseKey": ""
        },
        expirery: ''
      };
    },

    created: function () {
      stateService.addUpdateListener(this.updateAboutScreen.bind(this));
    },

    methods: {

      updateAboutScreen(data) {

        const version = stateService.getValue(data, config.swVersion);
        if (version) {
          this.version = version.major + '.' + version.minor + '.' +version.revision;
        }
        const stateSerialNumber = stateService.getValue(data, config.stateSerialNumber);
        if (stateSerialNumber) {
          this.robotSerialNumber = stateSerialNumber;
        }
        const license = stateService.getValue(data, config.stateLicense);
        if (license) {
          this.license = license;
        }
      },

    }

  };
</script>

<style scoped>

</style>



