import { WebSocketClass } from './WebSocketClass'
import { stateMessagePort } from '../../../config/configuration'

class stateServiceClass {

  constructor(){
    this.webSocket = new WebSocketClass();
    this.messageHandlers = [];
  }

  addUpdateListener(listener){
    this.messageHandlers.push(listener);
  }
  
  connect(){
    this.webSocket.connect(stateMessagePort, null, this.messageHandler.bind(this));
  }

  messageHandler(object){
    var i = 0;
    for (i=0; i<this.messageHandlers.length; i++){
      this.messageHandlers[i](object.data);
    }
  }
  
  getState() {
    return  this.webSocket.getState();
  }
  
  getValue(state, dotPath) {
    var value = null;
    const index = dotPath.indexOf('.');
    if (index > -1) {
      const firstProp = dotPath.substring(0, index);
      if (state.hasOwnProperty(firstProp)) {
        const restProps = dotPath.substr(index + 1);
        value = this.getValue(state[firstProp], restProps, value);
      }
    } else {
      if (state.hasOwnProperty(dotPath)) {
        value = state[dotPath];
      }
    }
    return value;
  }

  requestUpdate(dotPath){
    const request = {request: dotPath};
    this.webSocket.sendMessage(request);
  }
  
  setConnectionHandler(handler){
    this.webSocket.setConnectionHandler(handler);
  }


}

var stateService = new stateServiceClass();

export { stateService };



