<template>
  <div>
    <div v-if="robots.length > 0">
      <p>Click serial number to select a robot</p>
      <table>
        <tr @click="removeRobot(robot.serialNumber)" >
          <td>Status</td >
          <td>Serial number</td>
          <td>Host address</td>
          <td class="delete">Remove</td >
        </tr>

        <tr v-for="robot in robots" 
            v-bind:class="{ selected: selected === robot.serialNumber}"
            v-bind:id="robot.serialNumber">
          <td >
            {{robot.status}}
          </td>

          <td  v-on:click="selectRobot(robot.serialNumber)">
            {{robot.serialNumber}}
          </td>

          <td  >
            {{robot.host}}
          </td>

          <td class="delete">
            <md-button class="md-icon-button md-dense" v-on:click="removeRobot(robot.serialNumber, robot.host)">
              <md-icon >close_circle</md-icon>
            </md-button>
          </td>

        </tr>


      </table>
    </div>
    <div v-else>
      <p>No robots are configured</p>
    </div>
    <md-card md-with-hover>
      <md-card-header>
        <div class="md-title">New robot</div>
      </md-card-header>
      <md-card-expand>

        <md-card-content>
          <p>To add a robot, enter its network address.</p>
          <p>The Robot Dashboard must be installed on the robot in order to add the robot here.</p>
          <md-input-container>
            <label>Robot host address</label>
            <md-input v-model="host" placeholder="IP address or DNS name"></md-input>
          </md-input-container>

        </md-card-content>


        <md-card-actions md-alignment="left" class="urButtons">
          <md-button class="md-raised" :disabled="isHostEmpty()"  v-on:click="addRobot()">Add robot</md-button>
          <md-button class="md-icon-button" md-expand-trigger>
            <md-icon>keyboard_arrow_down</md-icon>
          </md-button>

        </md-card-actions>
      </md-card-expand>
    </md-card>
    <md-snackbar ref="snackbar" >
      <span> {{ errorMessage }} </span>
    </md-snackbar>
  </div>

</template>

<script>
  const config = require('../../../config/configuration');
  import { stateService } from '../services/stateService';
  import { DashboardServiceClass } from '../services/dashboardService';
  const dashboardService = new DashboardServiceClass();

  export default {
    
    name: 'portfolios',
    data() {
      return {
        host: '',
        robots: [],
        selected: '',
        errorMessage: '',
      };
    },

    created: function () {
      stateService.addUpdateListener(this.update.bind(this));
    },

    methods: {

      addRobot() {
        dashboardService.addRobot(this.host, this.robotAdded.bind(this));
      },

      selectRobot(serialNumber){
        this.selected = serialNumber;
        this.$emit('clicked', serialNumber);
      },
      
      removeRobot(serial, host){
        dashboardService.removeRobot(serial, host, this.robotRemoved.bind(this));
      },
      
      
      robotAdded(result) {
        this.errorMessage = '';
        if (result.success){
          this.host = '';
        } else {
          this.showSnack(result.error);
        }
      },

      robotRemoved(result) {
        this.errorMessage = '';
        if (result.success){
          // nothing
        } else {
          this.showSnack(result.error);
        }
      },

      showSnack(message){
          this.$refs.snackbar.open();
          this.errorMessage = message;
      },
      
      update(data) {
        const robots = stateService.getValue(data, config.stateRobots);
        if (robots) {
          this.robots = robots;
        }
      },

      isHostEmpty() {
        return this.host === '';
      },

    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .urButtons { display: block;}

  .delete { 
    text-align: center;
  }
  
  .selected {
      background: lightblue;
  }
  
  td {
    padding: 0.5em;
  }
</style>



