export class WebSocketClass {

  constructor(){
    this.socket = '';
    this.connection = null;
    this.connectTimer = null;
    this.connectCallback = null;
    this.dataHandler = null;
    this.connectionHandler = null;
    this.state = {
        connected: false,
        connectionUpdate: false,
    }
  }

  connect(port, done, dataHandler){
    this.disconnect();
    this.socket = this.makeSocketHost(port);
    this.connectCallback = done;
    this.dataHandler = dataHandler;
    if (this.connectTimer === null){
      this.connectTimer = setInterval(this.reconnect.bind(this), 1000);
    }
    this.reconnect();
  }

  disconnect(){
     this.connection = null;
     this.state.connected = false;
     this.state.connectionUpdate = false;
     if (this.connectionHandler){
       this.connectionHandler(false);
     }
  }

  establishConnection(){
    if (this.socket){
      var connection = new WebSocket(this.socket);

      connection.onclose = (event) => {
        this.disconnect();
      }

      connection.onopen = () => {
        this.state.connected = true;
        this.state.connectionUpdate = false;
        if (this.connectionHandler){
          this.connectionHandler(true);
        }
      }

      connection.onmessage = (message) => {
        var json = null;
        try {
          json = JSON.parse(message.data);
        } catch (e) {
          console.log('Invalid JSON: ', message.data);
        }
        this.dataHandler(json);
      };
      this.connection = connection;
      this.state.connectionUpdate = true;
    }
  }

  getState(){
    return this.state;
  }

  makeSocketHost(port){
    var host = window.location.host;
    var ix = host.indexOf(":");
    if (ix >= 0){
      host = host.substring(0,ix);
    }
    return 'ws://' + host + ':' + port;
  }

  reconnect(){
    if (this.state.connected){
    } else if (!this.state.connectionUpdate){
      this.establishConnection();
    }
  }

  sendMessage(request){
    this.connection.send(JSON.stringify(request));
  }
  
  setConnectionHandler(handler){
    this.connectionHandler = handler;
  }

}



