import { webServiceClass } from './webService';

// singleton data
const webService = new webServiceClass();

export class DashboardServiceClass {

  addRobot(host, done) {
    webService.API('PUT', 'add-robot?host=' + host, done);
  }

  addBackupAgent(serialNumber, name, file, frequency, keepForever, keepNumber, done) {
    webService.API('PUT', 'add-backup-agent?serialNumber=' + serialNumber
            + '&name=' + name + '&file=' + file + '&frequency=' + frequency
            + '&keepForever=' + keepForever + '&keepNumber=' + keepNumber, done);
  }

  downloadDoneBld(filename, done) {
    return function (response) {
      if (response.success) {
        console.log(response.success.headers)
        response.success.blob().then(blob =>
        {
          if (window.navigator.msSaveOrOpenBlob) {
            window.navigator.msSaveOrOpenBlob(blob, filename);
          } else {
            const a = document.createElement('a');
            document.body.appendChild(a);
            const url = window.URL.createObjectURL(blob);
            a.href = url;
            a.download = filename;
            a.click();
            // To be tested.
            // Firefox 32 bit fails for large files.
            // Schedule cleanup to after click.
            setTimeout(() => {
              window.URL.revokeObjectURL(url);
              document.body.removeChild(a);
            }, 0)
          }
          done(response);
        })
      } else {
        done(response);
      }
    }
  }

  deleteBackup(serialNumber, fullFilePath, done) {
    webService.API('PUT', 'delete-backup?serialNumber=' + serialNumber + '&fullFilePath=' + fullFilePath, done);
  }

  downloadBackup(serialNumber, fullFilePath, done) {
    const fileParts = fullFilePath.split('/');
    const filename = fileParts.pop();
    const callback = this.downloadDoneBld(filename, done);
    webService.BlobRequestAPI('GET', 'download-backup?serialNumber=' + serialNumber + '&fullFilePath=' + fullFilePath, callback);
  }

  removeBackupAgent(_id, done) {
    webService.API('PUT', 'remove-backup-agent?_id=' + _id, done);
  }

  runBackupAgent(_id, done) {
    webService.API('PUT', 'run-backup-agent?_id=' + _id, done);
  }

  removeRobot(serialNumber, host, done) {
    webService.API('PUT', 'remove-robot?serial_number=' + serialNumber + '&host=' + host, done);
  }

  setConnectionHandler(handler) {
    webService.setConnectionHandler(handler);
  }

}



