<template>
  <md-card md-with-hover>
    <md-card-header>
      <div class="md-title">New backup agent</div>
    </md-card-header>

    <md-card-expand>

      <md-card-content>
        <md-input-container>
          <label>Agent name</label>
          <md-input v-model="name"></md-input>
        </md-input-container>
        <md-input-container >
          <label>Backup file or directory</label>
          <md-input v-model="file"></md-input>
        </md-input-container>
        <label>Backup frequency</label>
        <div>
          <md-radio v-model="frequency" md-value="Hourly">Hourly</md-radio>
          <md-radio v-model="frequency" md-value="Daily">Daily</md-radio>
          <md-radio v-model="frequency" md-value="Weekly">Weekly</md-radio>
          <md-radio v-model="frequency" md-value="Manually">Manually</md-radio>
        </div>
        <md-checkbox v-model="keepForever">Keep backup forever</md-checkbox>
        <md-input-container v-if="!keepForever">
          <label>Number of backups to keep before deleting</label>
          <md-input min="1" v-model="keepNumber" type="number" ></md-input>
        </md-input-container>
      </md-card-content>

      <md-card-actions md-alignment="left" class="urButtons">
        <md-button class="md-raised" :disabled="!isNewAgentValid()" v-on:click="addBackupAgent()">Add agent</md-button>
        <md-button class="md-icon-button" md-expand-trigger>
          <md-icon>keyboard_arrow_down</md-icon>
        </md-button>
      </md-card-actions>
    </md-card-expand>
  </md-card>
</template>

<script>

  import { DashboardServiceClass } from '../services/dashboardService';
  const dashboardService = new DashboardServiceClass();

  export default {

    props: ['serialNumber'],

    name: 'backup',
    data() {
      return {
        errorMessage: '',
        name: '',
        file: '',
        frequency: 'Daily',
        keepForever: true,
        keepNumber: 20,
      };
    },

    methods: {

      addBackupAgent() {
        dashboardService.addBackupAgent(this.serialNumber, this.name, this.file, this.frequency, this.keepForever, this.keepNumber, this.backupAgentAdded.bind(this));
      },

      backupAgentAdded(result) {
        this.errorMessage = '';
        if (result.success) {
          this.name = '';
          this.file = '';
          this.frequency = 'Daily';
          this.keepForever = true;
          this.keepNumber = 20;
        } else {
          this.showSnack(result.error);
        }
      },

      isNewAgentValid() {
        const keepValid = this.keepForever || (this.keepNumber > 0);
        return keepValid
                && (this.name !== '')
                && (this.file !== '');
      },

      showSnack(message) {
        this.$emit('clicked', message);
      },

    }

  };
</script>

<style scoped>
  .urButtons { display: block;}

  .iconCell { 
    text-align: center;
  }

  .iconButton{
    position: static;
  }
</style>



