<template>
  <div id="app">
    <md-toolbar >
      <div style="float: right">
        <div style ="float: right">
          <h2 class="md-title">Robot Data Center</h2>
<p align="right" v-bind:class="{ disconnected: connectionError }">
<md-icon v-bind:class="{ connected: !connectionError }" >error</md-icon>
{{ hostStatus }} </p>
          
        </div>
        <img src="./assets/collaborative-robots-help-me-choose-big.png">
      <p v-if="serialNumber !== ''">Using robot {{ serialNumber }} </p>
      </div>
    </md-toolbar>
    <md-tabs>

      <md-tab md-label="Robots">
        <Robots v-on:clicked="serialNumberUpdate"></Robots>
      </md-tab>

      <md-tab md-label="Backups">
        <Backups v-bind:serialNumber="serialNumber" ></Backups>
      </md-tab>

      <md-tab md-label="About">
        <About></About>
      </md-tab>

    </md-tabs>

    <main class="mdl-layout__content">
      <div class="page-content">
      </div>
    </main>
  </div>
</template>

<script>

  import { stateService } from './services/stateService';
  import { DashboardServiceClass } from './services/dashboardService';

  const dashboardService = new DashboardServiceClass();
  const config = require('../../config/configuration');

  export default {
    name: 'app',
    data() {
      return {
        dnsname: 'localhost',
        hostStatus: 'none',
        connectionError: true,
        connectionWebsocket: false,
        connectionWebAPI: false,
        serialNumber: '',
      };
    },

    created: function () {
      dashboardService.setConnectionHandler(this.updateWebAPIConnectionState.bind(this));
      stateService.connect();
      stateService.setConnectionHandler(this.updateWebsocketConnectionState.bind(this));
    },

    methods: {

      serialNumberUpdate(value){
        this.serialNumber = value;
      },
    
      show() {
        this.connectionError = true;
        if (!this.connectionWebAPI) {
          this.hostStatus = "Waiting for WEB api";
        } else if (!this.connectionWebsocket) {
          this.hostStatus = "Waiting for data updates";
        } else {
          this.hostStatus = 'On-line';
          this.connectionError = false;
        }
      },

      updateWebAPIConnectionState(state) {
        this.connectionWebAPI = state;
        this.show();
      },

      updateWebsocketConnectionState(state) {
        this.connectionWebsocket = state;
        this.show();
      },

    },

  }
</script>

<style>
  img {
    width:10%;
    align: right;
  }

  .disconnected{
    background-color: lightcoral;
    padding: 5px;
  }
  .connected{
    visibility: hidden;
  }

</style>



